* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  width: 100%;
  height: 100vh;
  overflow: hidden;
  background-color: #BD10E0;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.Heart {
  width: 320px;
  font-family: Futura;
  font-size: 56px;
  position: absolute;
  top: 50%;
  left: 50%;
  color: #7EEA6D;
  transform: translate(-50%, -50%);
  text-align: center;

  .tooltip {
    font-size: 16px;
    color: black;
    opacity: 0;
  }
}
